import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

const MainContent = ({ selectedCity, selectedState, eggRates }) => {
  const displayName = selectedCity ? selectedCity : selectedState ? selectedState : 'India';
  const today = new Date().toLocaleDateString('en-US', {
    day: 'numeric',
    month: 'long',
    year: 'numeric'
  });
  
  const [featuredWebStories, setFeaturedWebStories] = useState([]);
  const [showWebStories, setShowWebStories] = useState(false);
  
  // Stats calculations
  const currentRate = eggRates.length > 0 ? eggRates[0].rate : 'N/A';
  // We'll use this in the future for additional comparisons
  // const rate7DaysAgo = eggRates.length > 7 ? eggRates[6].rate : 'N/A';
  const weeklyChange = eggRates.length > 7 ? (eggRates[0].rate - eggRates[6].rate).toFixed(2) : 'N/A';
  const weeklyChangePercent = eggRates.length > 7 ? ((eggRates[0].rate - eggRates[6].rate) / eggRates[6].rate * 100).toFixed(2) : 'N/A';
  const averagePrice = eggRates.length > 0 ? (eggRates.reduce((sum, rate) => sum + rate.rate, 0) / eggRates.length).toFixed(2) : 'N/A';

  useEffect(() => {
    // Fetch web stories and randomly select 3
    fetch('/php/get_web_stories.php')
      .then(response => response.json())
      .then(data => {
        const shuffled = [...data].sort(() => 0.5 - Math.random());
        setFeaturedWebStories(shuffled.slice(0, 3));
      })
      .catch(error => console.error('Error fetching web stories:', error));
  }, []);

  const getUniqueH1 = () => {
    if (selectedCity) {
      return `Egg Rate in ${selectedCity}, ${selectedState} (${today})`;
    } else if (selectedState) {
      return `Egg Rates in ${selectedState} (${today})`;
    }
    return `Today's Egg Rates in India (${today})`;
  };

  return (
    <div className="max-w-4xl mx-auto mb-8">
      {/* Hero Section */}
      <div className="bg-white rounded-lg shadow-md p-6 mb-6">
        <h1 className="text-3xl font-bold text-gray-800 text-center mb-4">
          {getUniqueH1()}
        </h1>
        <p className="text-center text-lg font-semibold text-gray-700 mb-2">
          Current Rates for {displayName}
        </p>
        <p className="text-center text-gray-600 mb-4">
          {selectedCity 
            ? `Get the latest egg rates for ${selectedCity}. Updated daily with wholesale and retail prices.`
            : selectedState
              ? `Check current egg prices across ${selectedState}. Compare rates from different cities.`
              : 'Track egg prices across India with our daily updated NECC rates from major cities.'
          }
        </p>

        {/* Price Statistics */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-6">
          <div className="bg-blue-50 p-4 rounded-lg">
            <h3 className="font-semibold text-blue-800">Today's Rate</h3>
            <p className="text-2xl font-bold text-blue-600">₹{currentRate}</p>
          </div>
          <div className="bg-green-50 p-4 rounded-lg">
            <h3 className="font-semibold text-green-800">Weekly Change</h3>
            <p className="text-2xl font-bold text-green-600">{weeklyChange} ({weeklyChangePercent}%)</p>
          </div>
          <div className="bg-purple-50 p-4 rounded-lg">
            <h3 className="font-semibold text-purple-800">Average Price</h3>
            <p className="text-2xl font-bold text-purple-600">₹{averagePrice}</p>
          </div>
        </div>
      </div>

      {/* Web Stories Section */}
      <div className="mt-6 text-center">
        <button
          onClick={() => setShowWebStories(!showWebStories)}
          className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition-colors"
        >
          {showWebStories ? 'Hide Web Stories' : 'Show Web Stories'}
        </button>
      </div>

      {showWebStories && featuredWebStories.length > 0 && (
        <div className="mt-6">
          <h2 className="text-2xl font-semibold text-gray-800 mb-4">Latest Updates</h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            {featuredWebStories.map(story => (
              <Link
                key={story.id}
                to={`/webstories/${story.slug}`}
                className="block bg-white p-4 rounded-lg shadow-md hover:shadow-lg transition-shadow duration-300"
              >
                <div className="aspect-w-9 aspect-h-16 mb-2">
                  <img 
                    src={story.thumbnail} 
                    alt={`Egg Rate in ${story.city}, ${story.state}`} 
                    className="object-cover w-full h-48 rounded-lg"
                    loading="lazy"
                  />
                </div>
                <h3 className="font-bold text-lg mb-1">{story.title}</h3>
                <p className="text-red-600 font-bold mb-1">₹{story.rate} per egg</p>
                <p className="text-gray-600 text-sm">{story.date}</p>
              </Link>
            ))}
          </div>          
        </div>
      )}

      {/* Market Information */}
      <div className="mt-8 bg-white rounded-lg shadow-md p-6">
        <h2 className="text-2xl font-semibold text-gray-800 mb-4">Market Information</h2>
        <div className="prose max-w-none">
          <p className="text-gray-700 mb-4">
            The National Egg Coordination Committee (NECC) plays a crucial role in determining egg prices in India. They consider factors like:
          </p>
          <ul className="list-disc pl-5 text-gray-700 mb-4">
            <li>Feed costs and availability</li>
            <li>Transportation expenses</li>
            <li>Market demand and supply</li>
            <li>Seasonal variations</li>
          </ul>
        </div>
      </div>

      {/* Additional Market Info */}
      <div className="mt-8 bg-white rounded-lg shadow-md p-6">
        <h2 className="text-2xl font-semibold text-gray-800 mb-4">Price Analysis</h2>
        <p className="text-gray-700 mb-4">
          {`Current price trends in ${displayName} show ${weeklyChange > 0 ? 'an increase' : 'a decrease'} compared to last week. 
          The average price over the monitored period is ₹${averagePrice}.`}
        </p>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="bg-gray-50 p-4 rounded-lg">
            <h3 className="font-semibold text-gray-800 mb-2">Weekly Comparison</h3>
            <p className="text-gray-700">
              {`Price ${weeklyChange > 0 ? 'increased' : 'decreased'} by ₹${Math.abs(weeklyChange)} (${Math.abs(weeklyChangePercent)}%) over the past week.`}
            </p>
          </div>
          <div className="bg-gray-50 p-4 rounded-lg">
            <h3 className="font-semibold text-gray-800 mb-2">Market Insight</h3>
            <p className="text-gray-700">
              {`Tray price (30 eggs): ₹${(currentRate * 30).toFixed(2)}`}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MainContent;
